<template>
  <div>
    <!-- 控制按钮 -->
    <div class="control">
      <input type="file" id="fileinput" style="display: none" />
      <a-button type="primary" icon="play-circle" @click="runit" :loading="runing">运行</a-button>
      <a-button type="primary" icon="delete" @click="clear">清空</a-button>
    </div>
    <div v-show="language !== 'pygame'">
      <p>输入参数：</p>
      <a-textarea v-model="stdin"></a-textarea>
    </div>
    <!-- 画布 -->
    <div id="mycanvas" v-show="language == 'pygame'"></div>
    <div>
      <p>运行输出：</p>
      <pre id="output">{{ out }}</pre>
    </div>
    <p>查看代码：</p>
    <a-form-item>
      <JCodeEditor
        v-if="editorType == 'codemirror'"
        language="python"
        v-model="code"
        :fullScreen="true"
        readOnly="true"
        placeholder="请输入代码"
        ref="codeEditor"
      />
      <AceEditor
        v-if="editorType == 'ace'"
        language="python"
        v-model="code"
        readOnly="true"
        :height="500"
        ref="codeEditor"
      />
    </a-form-item>
  </div>
</template>

<script>
import { saveAs } from "file-saver";
import JCodeEditor from "./JCodeEditor";
import AceEditor from "./AceEditor";
import "../python/skulpt.min.js";
import "../python/skulpt-stdlib.js";

import axios from "axios";

const Sk = window.Sk;

export default {
  name: "PythonEditor",
  components: {
    JCodeEditor,
    AceEditor,
  },
  props: {
    editorType: {
      type: String,
      default: "ace", //ace codemirror
    },
  },
  data() {
    return {
      language: "cpp",
      supportLanguages: [
        "cpp",
        "c",
        "python",
        "java",
        "php",
        "turtle",
        "pygame",
      ],
      languageSuffix: {
        cpp: ".cpp",
        c: ".c",
        python: ".py",
        java: ".java",
        php: ".php",
      },
      languageApi: {
        cpp: "/api/teaching/onlineCode/cpp",
        c: "/api/teaching/onlineCode/c",
        python: "/api/teaching/onlineCode/python",
        java: "/api/teaching/onlineCode/java",
        php: "/api/teaching/onlineCode/php",
      },
      code: "",
      out: "",
      stdin: "",
      runing: false
    };
  },
  mounted() {
    var url = this.urlParam("url");
    if (url) {
      this.downloadFile(url);
    }
    this.language = this.urlParam("lang");
    if (!this.supportLanguages.includes(this.language)) {
      this.$message.error("不支持该语言");
    }
  },
  created() {},
  methods: {
    runit() {
      if (["pygame", "turtle"].includes(this.language)) {
        this.runWithSk();
      } else {
        this.runWithApi();
      }
    },
    runWithApi() {
      var that = this;
      this.clear();
      that.runing = true;
      axios
        .post(this.languageApi[this.language], {
          files: [
            {
              name:
                (this.projectName ? this.projectName : "Main") +
                this.languageSuffix[this.language],
              content: this.code,
            },
          ],
          stdin: that.stdin,
        })
        .then((res) => {
          if (res.data.error != "") {
            this.$notification.open({
              message: res.data.error,
              description: res.data.message || res.data.stderr,
              icon: <a-icon type="sad" style="color: #108ee9" />,
            });
          }
          that.terminalOut(res.data.stdout);
          that.runing = false;
        });
    },
    runWithSk() {
      var that = this;
      this.clear();
      Sk.pre = "output";
      Sk.configure({ output: that.terminalOut, read: that.builtinRead });
      (Sk.TurtleGraphics || (Sk.TurtleGraphics = {})).target = "mycanvas";
      var myPromise = Sk.misceval.asyncToPromise(function () {
        return Sk.importMainWithBody("<stdin>", false, that.getCode(), true);
      });
      myPromise.then(
        function (mod) {
          that.$message.success("执行完毕");
        },
        function (err) {
          that.$message.error(err.toString());
        }
      );
    },
    downloadFile(url) {
      var that = this;
      axios.get(url).then((res) => {
        that.setCode(res.data);
      });
    },
    openFile() {
      var that = this;
      var fileInput = document.getElementById("fileinput");
      fileInput.addEventListener("change", function (e) {
        const reader = new FileReader();
        const thisFileInput = e.target;
        reader.onload = function () {
          that.setCode(this.result);
          that.code = this.result;
        };
        reader.readAsText(thisFileInput.files[0]);
      });
      fileInput.click();
    },
    saveFile() {
      var n = new File([this.code], "python项目.py", {
        type: "text/plain;charset=utf-8",
      });
      saveAs(n);
    },
    submitCode() {
      if (window.submitCode) {
        window.submitCode(this.code);
      }
    },
    getCode() {
      this.code = this.$refs.codeEditor.getCodeContent();
      return this.code;
    },
    setCode(code) {
      this.code = code;
      this.$refs.codeEditor.setCodeContent(code);
    },
    clear() {
      document.getElementById("output").innerHTML = "";
      document.getElementById("mycanvas").innerHTML = "";
    },
    terminalOut(v) {
      var mypre = document.getElementById("output");
      mypre.innerHTML = mypre.innerHTML + v;
    },
    builtinRead(n) {
      if (
        Sk.builtinFiles === undefined ||
        Sk.builtinFiles.files[n] === undefined
      )
        throw `File not found: '${n}'`;
      return Sk.builtinFiles.files[n];
    },
    urlParam(name) {
      var results = new RegExp("[\\?&]" + name + "=([^&#]*)").exec(
        window.location.href
      );
      if (!results) {
        return 0;
      }
      return results[1] || 0;
    },
  },
};
</script>

<style lang="less">
.CodeMirror {
  /* Set height, width, borders, and global font properties here */
  font-family: monospace;
  // height: calc(100vh) !important;
  color: black;
  direction: ltr;
}
#output {
  border: 1px solid black;
  height: 10rem;
  width: 100%;
  padding: 10px;
  margin: 10px 0;
}

#mycanvas {
  border: 1px solid black;
  height: 40vh;
  width: 100%;
}
.control {
  margin: 10px;
  button {
    margin: 10px 0;
  }
}
.ant-layout-header {
  background: #dfefff;
}
.ant-layout-sider {
  padding: 10px;
}
</style>


